const animationsCollection = {
    animationShangtsungVictory2,
    animationSindelVictory2,
    animationJaxVictory2,
    animationKanoVictory2,
    animationLiukangVictory2,
    animationSonyaVictory2,
    animationStrykerVictory2,
    animationSmokeVictory2,
    animationSubzeroVictory2,
    animationCyraxVictory2,
    animationSektorVictory2,
    animationNightwolfVictory2,
    animationSheevaVictory2,
    animationKunglaoVictory2,
    animationKabalVictory2,
    animationMotaroVictory2,
    animationShaokahnVictory2,
};

const soundsHeroCollection = {
    soundShangtsung,
    soundSindel,
    soundJax,
    soundKano,
    soundLiukang,
    soundSonya,
    soundStryker,
    soundSmoke,
    soundSubzero,
    soundCyrax,
    soundSektor,
    soundNightwolf,
    soundSheeva,
    soundKunglao,
    soundKabal,
    soundMotaro,
    soundShaokahn,
};

class Menu {
    constructor({menuHolder, menu, title, loader, biography}) {        
        this.$menuHolder = menuHolder;
        this.$menu = menu;
        this.$title = title;
        this.$loader = loader;        
        this.$biography = biography;        
        this.player1Img = null; // –ü—É—Ç—å –¥–æ –ø–æ—Ç–æ—á–Ω–æ—ó –∫–∞—Ä—Ç–∏–Ω–∫–∏ –≥–µ—Ä–æ—è —â–æ –≤—ñ–¥–æ–±—Ä–∞–∂–∞—î—Ç—å—Å—è –Ω–∞ –ø–æ—Å—Ç–∞–º–µ–Ω—Ç—ñ –¥–ª—è –≥—Ä–∞–≤—Ü—è ‚Ññ1 (–∫–æ–ª–∏ –∫—É—Ä—Å–æ—Ä –≤–∏—Ö–æ–¥–∏—Ç—å –∑–∞ –º–µ–∂—ñ –º–µ–Ω—é –ø–æ—Å—Ç–∞–º–µ–Ω—Ç –º–∞—î –æ—á–∏—â–∞—Ç–∏—Å—è).         
        this.player2Img = null; // –ü—É—Ç—å –¥–æ –ø–æ—Ç–æ—á–Ω–æ—ó –∫–∞—Ä—Ç–∏–Ω–∫–∏ –≥–µ—Ä–æ—è —â–æ –≤—ñ–¥–æ–±—Ä–∞–∂–∞—î—Ç—å—Å—è –Ω–∞ –ø–æ—Å—Ç–∞–º–µ–Ω—Ç—ñ –¥–ª—è –≥—Ä–∞–≤—Ü—è ‚Ññ2 (–∫–æ–ª–∏ –∫—É—Ä—Å–æ—Ä –≤–∏—Ö–æ–¥–∏—Ç—å –∑–∞ –º–µ–∂—ñ –º–µ–Ω—é –ø–æ—Å—Ç–∞–º–µ–Ω—Ç –º–∞—î –æ—á–∏—â–∞—Ç–∏—Å—è).         
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø—Ä–∏—Ö–æ–≤—É–≤–∞–Ω–Ω—è —Å—Ç–∞—Ä—Ç–æ–≤–æ–≥–æ –ª–æ–∞–¥–µ—Ä–∞.
     */
    hideStartLoader() {   
        this.$loader.classList.add('disabled');                
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø—Ä–∏—Ö–æ–≤—É–≤–∞–Ω–Ω—è –º–µ–Ω—é.
     */
    hideMenu = () => {
        this.$menuHolder.classList.add('disabled');
    }
    
    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è –º–µ–Ω—é.
     */
    showMenu = () => {
        this.$menuHolder.classList.remove('disabled');
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è —Å–ø–ª–∏–≤–∞—é—á–æ–≥–æ –≤—ñ–∫–Ω–∞ –±—ñ–æ–≥—Ä–∞—Ñ—ñ—è.
     */
    showBiographyPopup = () => {
        this.$biography.classList.remove('disabled');
    }
    
    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø—Ä–∏—Ö–æ–≤—É–≤–∞–Ω–Ω—è —Å–ø–ª–∏–≤–∞—é—á–æ–≥–æ –≤—ñ–∫–Ω–∞ –±—ñ–æ–≥—Ä–∞—Ñ—ñ—è.
     */
    hideBiographyPopup = () => {
        this.$biography.classList.add('disabled');
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –±–ª–æ–∫—É–≤–∞–Ω–Ω—è –≤–∏–±–æ—Ä—É –≥–µ—Ä–æ—ó–≤ –≤ –º–µ–Ω—é.
     */
    blockPlayersSelection = () => {
        this.$menu.classList.add('blocked');
    };
    
    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∞–∫—Ç–∏–≤–∞—Ü—ñ—ó –≤–∏–±–æ—Ä—É –≥–µ—Ä–æ—ó–≤ –≤ –º–µ–Ω—é.
     */
    unlockPlayersSelection = () => {
        this.$menu.classList.remove('blocked');        
    };

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ—á–∏—â–µ–Ω–Ω—è localStorage –≤—ñ–¥ –¥–∞–Ω–∏—Ö –≥–µ—Ä–æ—ó–≤.
     */
    clearLocalStorage = () => {
        localStorage.removeItem('player1Id'); // –í–∏–¥–∞–ª—è—î–º–æ –¥–∞–Ω—ñ –≥–µ—Ä–æ—è –∑ –ø–∞–º'—è—Ç—ñ —è–∫—â–æ –≤–æ–Ω–∏ –±—É–ª–∏.
        localStorage.removeItem('player2Id'); // –í–∏–¥–∞–ª—è—î–º–æ –¥–∞–Ω—ñ –≥–µ—Ä–æ—è –∑ –ø–∞–º'—è—Ç—ñ —è–∫—â–æ –≤–æ–Ω–∏ –±—É–ª–∏.
    };

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è —Å–∏–Ω—Ö—Ä–æ–Ω–∏–∑–∞—Ü—ñ—ó –∑–∞–ø–∏—Å—ñ–≤ –ø—Ä–æ —ñ–¥–µ–Ω—Ç–∏—Ñ—ñ–∫–∞—Ç–æ—Ä–∏ –æ–±—Ä–∞–Ω–∏—Ö –≥–µ—Ä–æ—ó–≤ –≤ –∫–ª–∞—Å—ñ –º–µ–Ω—é –∑ –ª–æ–∫–∞–ª—å–Ω–∏–º —Å—Ö–æ–≤–∏—â–µ–º.
     */
    syncWithLocalStorage = playersCollectionNumber => {
        this[`player${playersCollectionNumber}IdLocalStorage`] = +localStorage.getItem(`player${playersCollectionNumber}Id`); // –ó–±–µ—Ä—ñ–≥–∞—î–º–æ –∑–∞–ø–∏—Å –ø—Ä–æ —ñ–¥–µ–Ω—Ç–∏—Ñ—ñ–∫–∞—Ç–æ—Ä –æ–±—Ä–∞–Ω–æ–≥–æ –≥–µ—Ä–æ—è —ñ–∑ –ª–æ–∫–∞–ª—å–≥–æ —Å—Ö–æ–≤–∏—â–∞ –≤ –∫–ª–∞—Å –º–µ–Ω—é. 
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è —Å—Ç–≤–æ—Ä–µ–Ω–Ω—è –∞–≤–∞—Ç–∞—Ä–∫–∏ –≤ –≥–æ–ª–æ–≤–Ω–æ–º—É –º–µ–Ω—é.
     * 
     * @param {object} params - –ü–∞—Ä–∞–º–µ—Ç—Ä–∏ –¥–ª—è —Å—Ç–≤–æ—Ä–µ–Ω–Ω—è –∞–≤–∞—Ç–∞—Ä–∫–∏ src, alt, className. 
     * @param {string=} params.src - –ü—É—Ç—å –¥–æ –∞–≤–∞—Ç–∞—Ä–∫–∏.
     * @param {string=} params.alt - –ê–ª—å—Ç –¥–ª—è –∞–≤–∞—Ç–∞—Ä–∫–∏.
     * @param {string} params.className - –Ü–º'—è –∫–ª–∞—Å—É –µ–ª–µ–º–µ–Ω—Ç—É.
     * @returns {HTMLElement} - –ê–≤–∞—Ç–∞—Ä –∞–±–æ –ª–æ–≥–æ—Ç–∏–ø. 
     */
    createMenuItem = ({src = imgLogo, alt = 'Mortal Kombat', className}) => {
        const $el = createNewTag({
            tagName: 'div',
            className: ['avatar', `div${className}`],
        });

        const $img = createNewTag({tagName: 'img'});
       
        $img.src = src;
        $img.alt = alt;

        if (className === '-logo') {
            const $a = createNewTag({
                tagName: 'a', 
                attrList: {
                    href: '/',
                },                 
            });

            $a.append($img);
            $el.append($a);
        } else {
            $el.append($img);
        }

        return $el;
    };

    /**
     *  –°—Ç–≤–æ—Ä–µ–Ω–Ω—è –ø–æ—Å—Ç–∞–º–µ–Ω—Ç—É –¥–ª—è –≥–µ—Ä–æ—è.      
     * 
     * @param {number} playersCollectionNumber - –ù–æ–º–µ—Ä –∫–æ–ª–µ–∫—Ü—ñ—ó –≥–µ—Ä–æ—ó–≤ (1 - —Ü–µ –∫–æ–ª–µ–∫—Ü—ñ—è –≥–µ—Ä–æ—ó–≤ –¥–ª—è –∂–∏–≤–æ–≥–æ –≥—Ä–∞–≤—Ü—è –∞–±–æ 2 - —Ü–µ –∫–æ–ª–µ–∫—Ü—ñ—è –≥–µ—Ä–æ—ó–≤ –¥–ª—è –∫–æ–º–ø'—é—Ç–µ—Ä–∞).
     * @returns {HTMLElement} - HTML –µ–ª–µ–º–µ–Ω—Ç, –ø–æ—Å—Ç–∞–º–µ–Ω—Ç –¥–ª—è –≥–µ—Ä–æ—è.
     */
    createPedestal = playersCollectionNumber => {
        return createNewTag({
            tagName: 'div',
            className: 'pedestal' + playersCollectionNumber,
        });
    };

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∑–º—ñ–Ω–∏ –ø–æ–∑–∏ –Ω–∞ –ø–æ—Å—Ç–∞–º–µ–Ω—Ç—ñ.
     * 
     * @param {string} srcImg - –ü—É—Ç—å –¥–æ –∫–∞—Ä—Ç–∏–Ω–∫–∏ –∑ –Ω–æ–≤–æ—é –ø–æ–∑–æ—é.
     * @param {string} playerName - –Ü–º'—è –≥–µ—Ä–æ—è.
     * @param {number} playersCollectionNumber - –ù–æ–º–µ—Ä –∫–æ–ª–µ–∫—Ü—ñ—ó –≥–µ—Ä–æ—ó–≤ (1 - —Ü–µ –∫–æ–ª–µ–∫—Ü—ñ—è –≥–µ—Ä–æ—ó–≤ –¥–ª—è –∂–∏–≤–æ–≥–æ –≥—Ä–∞–≤—Ü—è –∞–±–æ 2 - —Ü–µ –∫–æ–ª–µ–∫—Ü—ñ—è –≥–µ—Ä–æ—ó–≤ –¥–ª—è –∫–æ–º–ø'—é—Ç–µ—Ä–∞).
     */
    changePosition = (srcImg, playerName, playersCollectionNumber) => {
        const $pedestal = this[`$pedestal${playersCollectionNumber}`];
        const playerSrc = `player${playersCollectionNumber}Img`;
        let src = srcImg;

        this[playerSrc] = (playersCollectionNumber === 2) ? src = animationsCollection[`animation${playerName}Victory${playersCollectionNumber}`] : src; // –¢—Ä–µ–±–∞ —â–æ–± –ø–æ–≤—Ç–æ—Ä–Ω–æ –∑ –ø–æ—á–∞—Ç–∫—É –ø—Ä–æ–≥—Ä–∞–≤–∞–ª–∏—Å—å –∫–∞—Ä—Ç–∏–Ω–∫–∏ –æ–±—Ä–∞–Ω–∏—Ö –æ–¥–∏–Ω–∞–∫–æ–≤–∏—Ö –≥–µ—Ä–æ—ó–≤.
        this.upgradePedestal($pedestal, src, playersCollectionNumber, true);
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∑–¥—ñ–π—Å–Ω–µ–Ω–Ω—è –æ–Ω–æ–≤–ª–µ–Ω–Ω—è –ø–æ—Å—Ç–∞–º–µ–Ω—Ç—É. 
     * 
     * @param {HTMLElement} $pedestal - –ï–ª–µ–º–µ–Ω—Ç –ø–æ—Å—Ç–∞–º–µ–Ω—Ç. 
     * @param {string} src - –ü—É—Ç—å –¥–æ –∫–∞—Ä—Ç–∏–Ω–∫–∏.
     * @param {number} playersCollectionNumber - –ù–æ–º–µ—Ä –∫–æ–ª–µ–∫—Ü—ñ—ó –≥–µ—Ä–æ—ó–≤ (1 - —Ü–µ –∫–æ–ª–µ–∫—Ü—ñ—è –≥–µ—Ä–æ—ó–≤ –¥–ª—è –∂–∏–≤–æ–≥–æ –≥—Ä–∞–≤—Ü—è –∞–±–æ 2 - —Ü–µ –∫–æ–ª–µ–∫—Ü—ñ—è –≥–µ—Ä–æ—ó–≤ –¥–ª—è –∫–æ–º–ø'—é—Ç–µ—Ä–∞).
     * @param {boolean} [update] - –ú—ñ—Ç–∫–∞ —è–∫–∞ –≤–∫–∞–∑—É—î –Ω–∞ –Ω–µ–æ–±—Ö—ñ–¥–Ω—ñ—Å—Ç—å –≤ –æ–Ω–æ–≤–ª–µ–Ω–Ω—ñ –ø–æ—Å—Ç–∞–º–µ–Ω—Ç—É. 
     */
    upgradePedestal = ($pedestal, src, playersCollectionNumber, update) => {
        const playerImg = `player${playersCollectionNumber}Img`;  
        
        if (this[playerImg] === null || update) {            
            const $img = createNewTag({tagName: 'img'});                
            
            this[playerImg] = src;            
            $img.src = this[playerImg];
            
            $pedestal.innerHTML = '';
            $pedestal.append($img);
        }
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ—á–∏—â–µ–Ω–Ω—è –ø–æ—Å—Ç–∞–º–µ–Ω—Ç—É –ø—ñ—Å–ª—è –ø—Ä–∏–±–∏—Ä–∞–Ω–Ω—è –∫—É—Ä—Å–æ—Ä—É –∑ –æ–±–ª–∞—Å—Ç—ñ –∞–≤–∞—Ç–∞—Ä–∫–∏.
     * 
     * @param {HTMLElement} $avatar - –ï–ª–µ–º–µ–Ω—Ç –∞–≤–∞—Ç–∞—Ä.
     * @param {HTMLElement} $pedestal - –ï–ª–µ–º–µ–Ω—Ç –ø–æ—Å—Ç–∞–º–µ–Ω—Ç (pedestal1 –∞–±–æ pedestal2).
     */
    clearPedestal = ($avatar, $pedestal) => {
        $avatar.addEventListener('mouseout', () => {
            if (!this.player1IdLocalStorage && this.player1Img || this.player1Img === undefined) {            
                this.player1Img = null;
                $pedestal.innerHTML = '';
            } else if (this.player1IdLocalStorage && !this.player2IdLocalStorage && this.player2Img || this.player2Img === undefined) {
                this.player2Img = null;
                $pedestal.innerHTML = '';
            }
        });
    };

    /** 
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è —Å—Ç–≤–æ—Ä–µ–Ω–Ω—è –≥–∞–ª–µ—Ä–µ—ó –∑ –∞–≤–∞—Ç–∞—Ä–æ–∫. 
     * 
     * @param {object[]} playersCollection - –ö–æ–ª–µ–∫—Ü—ñ—è –≥–µ—Ä–æ—ó–≤, –º–∞—Å–∏–≤ –æ–±'—î–∫—Ç—ñ–≤ –∑ –≥–µ—Ä–æ—è–º–∏.
     * @param {number} playersCollectionNumber - –ù–æ–º–µ—Ä –∫–æ–ª–µ–∫—Ü—ñ—ó –≥–µ—Ä–æ—ó–≤ (1 - —Ü–µ –∫–æ–ª–µ–∫—Ü—ñ—è –≥–µ—Ä–æ—ó–≤ –¥–ª—è –∂–∏–≤–æ–≥–æ –≥—Ä–∞–≤—Ü—è –∞–±–æ 2 - —Ü–µ –∫–æ–ª–µ–∫—Ü—ñ—è –≥–µ—Ä–æ—ó–≤ –¥–ª—è –∫–æ–º–ø'—é—Ç–µ—Ä–∞).
     */
    createGalleryMenu = (playersCollection, playersCollectionNumber) => {        
        if (!this.$logo) {
            this.$logo = this.createMenuItem({className: '-logo'}); // –°—Ç–≤–æ—Ä–µ–Ω–Ω—è –ª–æ–≥–æ—Ç–∏–ø—É –¥–ª—è –º–µ–Ω—é.
        }

        const $logo = this.$logo;
        const $pedestal = this[`$pedestal${playersCollectionNumber}`] = this.createPedestal(playersCollectionNumber); // –°—Ç–≤–æ—Ä—é—î–º–æ –ø–æ—Å—Ç–∞–º–µ–Ω—Ç –¥–ª—è –≥–µ—Ä–æ—è.
        
        playersCollection.forEach((item, index) => {         
            const $avatar = this.createMenuItem({ // –°—Ç–≤–æ—Ä–µ–Ω–Ω—è –∞–≤–∞—Ç–∞—Ä–∫–∏.
                src: item.avatar, 
                alt: item.name, 
                className: index + 1,
            });

            $avatar.setAttribute('data-players-collection', playersCollectionNumber);
                        
            this.lookPlayer($avatar, $pedestal, item.stance, playersCollectionNumber);        
            this.selectPlayer($avatar, item);        
            this.clearPedestal($avatar, $pedestal);   

            this.$menu.append($avatar);      
        });       

        this.$menu.append($logo);
        this.$menu.append($pedestal);
    };

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø–µ—Ä–µ–≥–ª—è–¥—É –≥–µ—Ä–æ—è —Ç–∞ –¥–æ–¥–∞–≤–∞–Ω–Ω—è –π–æ–≥–æ –Ω–∞ –ø–æ—Å—Ç–∞–º–µ–Ω—Ç.
     * 
     * @param {HTMLElement} $avatar - –ï–ª–µ–º–µ–Ω—Ç –∞–≤–∞—Ç–∞—Ä.
     * @param {HTMLElement} $pedestal - –ï–ª–µ–º–µ–Ω—Ç –ø–æ—Å—Ç–∞–º–µ–Ω—Ç (player1 –∞–±–æ player2).
     * @param {string} src - –ü—É—Ç—å –¥–æ –∫–∞—Ä—Ç–∏–Ω–∫–∏ –∞–≤–∞—Ç–∞—Ä—É.
     * @param {number} playersCollectionNumber - –ù–æ–º–µ—Ä –∫–æ–ª–µ–∫—Ü—ñ—ó –≥–µ—Ä–æ—ó–≤ (1 - —Ü–µ –∫–æ–ª–µ–∫—Ü—ñ—è –≥–µ—Ä–æ—ó–≤ –¥–ª—è –∂–∏–≤–æ–≥–æ –≥—Ä–∞–≤—Ü—è –∞–±–æ 2 - —Ü–µ –∫–æ–ª–µ–∫—Ü—ñ—è –≥–µ—Ä–æ—ó–≤ –¥–ª—è –∫–æ–º–ø'—é—Ç–µ—Ä–∞).
     */
    lookPlayer = ($avatar, $pedestal, src, playersCollectionNumber) => {  
        $avatar.addEventListener('mouseover', () => {
            this.upgradePedestal($pedestal, src, playersCollectionNumber);

            if(!$avatar.closest('.blocked')) { // –ó–≤—É–∫ —Ö–æ–≤–µ—Ä–∞ –º–µ–Ω—é.    
                createSoundTrack({
                    className: 'sound-menu-hover',
                    src: soundMainMenuHover,
                });
            }            
        });
    };
    
    /** 
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤–∏–±–æ—Ä—É –≥–µ—Ä–æ—è –ø—Ä–∏ –Ω–∞—Ç–∏—Å–∫–∞–Ω–Ω—ñ –ø–æ –Ω—å–æ–º—É. 
     * 
     * @param {HTMLElement} $avatar - –ï–ª–µ–º–µ–Ω—Ç –∞–≤–∞—Ç–∞—Ä.
     * @param {object} item - –û–±'—î–∫—Ç –∑ –¥–∞–Ω–∏–º–∏ –≥–µ—Ä–æ—è.
     */
    selectPlayer($avatar, item) {
        $avatar.addEventListener('click', () => {
            if (this.player1IdLocalStorage && !this.player2IdLocalStorage && this.player2Img) { // –í–∏–±—ñ—Ä –≥–µ—Ä–æ—è –¥–ª—è –≤–æ—Ä–æ–≥–∞.                                                                                                
                this.savingPlayer($avatar, item, 2);
                this.createBiographyPopup();
                this.beforeStartingGame();  
            } else if (!this.player1IdLocalStorage && !this.player2IdLocalStorage) { // –í–∏–±—ñ—Ä —Å–≤–æ–≥–æ –≥–µ—Ä–æ—è.               
                this.savingPlayer($avatar, item, 1);               
                    
                setTimeout(() => {
                    this.$title.innerHTML = 'SELECT A FIGHTER FOR YOUR ENEMY';
                    this.replacePlayersCollection();
                    this.unlockPlayersSelection();
                    this.activateMarker();
                }, 3000);
            }  

            clearGarbageTags('.sound-menu-hover');
        });
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∑–±–µ—Ä–µ–∂–µ–Ω–Ω—è –æ–±—Ä–∞–Ω–æ–≥–æ –≥–µ—Ä–æ—è –≥—Ä–∞–≤—Ü–µ–º.
     * 
     * @param {HTMLElement} $avatar - –ï–ª–µ–º–µ–Ω—Ç –∞–≤–∞—Ç–∞—Ä –æ–±—Ä–∞–Ω–æ–≥–æ –≥–µ—Ä–æ—è.
     * @param {object} item - –û–±'—î–∫—Ç –∑ –¥–∞–Ω–∏–º–∏ –ø—Ä–æ –≥–µ—Ä–æ—è —ñ–∑ –∫–æ–ª–µ–∫—Ü—ñ—ó –≥–µ—Ä–æ—ó–≤.
     * @param {number} playersCollectionNumber - –ù–æ–º–µ—Ä –∫–æ–ª–µ–∫—Ü—ñ—ó –≥–µ—Ä–æ—ó–≤ (1 - —Ü–µ –∫–æ–ª–µ–∫—Ü—ñ—è –≥–µ—Ä–æ—ó–≤ –¥–ª—è –∂–∏–≤–æ–≥–æ –≥—Ä–∞–≤—Ü—è –∞–±–æ 2 - —Ü–µ –∫–æ–ª–µ–∫—Ü—ñ—è –≥–µ—Ä–æ—ó–≤ –¥–ª—è –∫–æ–º–ø'—é—Ç–µ—Ä–∞).
     */
    savingPlayer = ($avatar, item, playersCollectionNumber) => {
        localStorage.setItem(`player${playersCollectionNumber}Id`, JSON.stringify(item.id)); // –î–æ–¥–∞—î–º–æ –∑–∞–ø–∏—Å –ø—Ä–æ –≤–∏–±—Ä–∞–Ω–æ–≥–æ –≥–µ—Ä–æ—è –≤ localStorage.
        $avatar.classList.add(`active${playersCollectionNumber}`); // –î–æ–¥–∞—î–º–æ –∫–ª–∞—Å active –≤–∏–±—Ä–∞–Ω–æ–º—É –∞–≤–∞—Ç–∞—Ä—É.        
        this.blockPlayersSelection(); // –í—ñ–¥–∫–ª—é—á–∞—î–º–æ –º–æ–∂–ª–∏–≤—ñ—Å—Ç—å –≤–∏–±–æ—Ä—É –≥–µ—Ä–æ—ó–≤ –≤ –º–µ–Ω—é.
        this.changePosition(item.victory, item.name, playersCollectionNumber); // –ó–º—ñ–Ω—é—î–º–æ –ø–æ–∑—É –≥–µ—Ä–æ—è –ø—ñ—Å–ª—è –∑–±–µ—Ä–µ–∂–µ–Ω–Ω—è –≤–∏–±–æ—Ä—É.
        this.syncWithLocalStorage(playersCollectionNumber); // –ó–±–µ—Ä—ñ–≥–∞—î–º–æ –∑–∞–ø–∏—Å –ø—Ä–æ —ñ–¥–µ–Ω—Ç–∏—Ñ—ñ–∫–∞—Ç–æ—Ä –æ–±—Ä–∞–Ω–æ–≥–æ –≥–µ—Ä–æ—è —ñ–∑ –ª–æ–∫–∞–ª—å–≥–æ —Å—Ö–æ–≤–∏—â–∞ –≤ –∫–ª–∞—Å –º–µ–Ω—é. 
        this[`player${playersCollectionNumber}`] = getPlayer(this[`playersCollection${playersCollectionNumber}`], this[`player${playersCollectionNumber}IdLocalStorage`]); // –ó–±–µ—Ä—ñ–≥–∞—î–º–æ –æ–±'—î–∫—Ç –∑ –¥–∞–Ω–∏–º–∏ –ø—Ä–æ –≥–µ—Ä–æ—è –≤ –∫–ª–∞—Å –º–µ–Ω—é.         
        this.$title.innerHTML = `YOUR FIGHTER - ${item.name.toUpperCase()} (Player-${playersCollectionNumber})`; // –ó–º—ñ–Ω—é—î–º–æ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è –≤ –∑–∞–≥–æ–ª–æ–≤–∫—ñ.

        createSoundTrack({
            className: 'sound-select-player',
            src: soundsHeroCollection[`sound${item.name}`],
        });
    };

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∑–º—ñ–Ω–∏ –∫–æ–ª–µ–∫—Ü—ñ—ó –≥–µ—Ä–æ—ó–≤ –Ω–∞ —Ç—É –≤ —è–∫—ñ–π –≤–∏–±—ñ—Ä –≥–µ—Ä–æ—è —â–µ –Ω–µ –∑–¥—ñ–π—Å–Ω—é–≤–∞–≤—Å—è.
     */
    replacePlayersCollection = () => {
        this.$menu.setAttribute('data-players-collection', 2);
    };    

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è –æ–±—Ä–∞–Ω–æ–≥–æ –≥–µ—Ä–æ—è —ñ–∑ –∫–æ–ª–µ–∫—Ü—ñ—ó –∂–∏–≤–æ–≥–æ –≥—Ä–∞–≤—Ü—è –≤ –∫–æ–ª–µ–∫—Ü—ñ—ó –∫–æ–º–ø'—é—Ç–µ—Ä–∞.
     */
    activateMarker = () => {
        const player1Collection2 = this.$menu.querySelector(`.div${this.player1IdLocalStorage}[data-players-collection='2']`); // –ó–Ω–∞—Ö–æ–¥–∏–º–æ –≤–∏–±—Ä–∞–Ω–æ–≥–æ player1 –≤ playersCollection2.

        player1Collection2.classList.add(`active1`); // –†–æ–±–∏–º–æ –∞–∫—Ç–∏–≤–Ω–∏–º –≤–∏–±—Ä–∞–Ω–æ–≥–æ player1 –≤ playersCollection2.
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –Ω–∞–ø–æ–≤–Ω–µ–Ω–Ω—è —Å–ø–ª–∏–≤–∞—é—á–æ–≥–æ –≤—ñ–∫–Ω–∞ –±—ñ–æ–≥—Ä–∞—Ñ—ñ—è.
     */
    createBiographyPopup = () => {
        const $player1Img = createNewTag({
            tagName: 'img',
            className: 'player1',
        });
        
        const $player2Img = createNewTag({
            tagName: 'img',
            className: 'player2',
        });

        $player1Img.src = this.player1.biography;
        $player2Img.src = this.player2.biography;
        $player1Img.alt = this.player1.name;
        $player2Img.alt = this.player2.name;

        this.$biography.append($player1Img);
        this.$biography.append($player2Img);
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø–µ—Ä–µ–Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–Ω—è —É –≥—Ä—É —Ç–∞ –∑–∞–≤–µ—Ä—à–µ–Ω–Ω—è —Ä–æ–±–æ—Ç–∏ –≤ –º–µ–Ω—é.
     */
    async beforeStartingGame() {
        await delay(3000).then(() => this.hideMenu());
        await delay(700).then(() => this.showBiographyPopup());
        await delay(3000).then(() => this.hideBiographyPopup());
        await delay(1000).then(() => hidePage());
        await delay(1000).then(() => window.location.pathname = 'kombat.html'); // –ü–µ—Ä–µ–∞–¥—Ä–µ—Å–∞—Ü—ñ—è –Ω–∞ –≥—Ä—É.
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∑–∞–ø—É—Å–∫—É –≥—Ä–∏.
     */
    async init() {
        this.clearLocalStorage(); // –ß–∏—Å—Ç–∏–º–æ –ª–æ–∫–∞–ª—å–Ω–µ —Å—Ö–æ–≤–∏—â–µ.
        this.syncWithLocalStorage(1); // –°—Ç–≤–æ—Ä—é—î–º–æ –∑–∞–ø–∏—Å –≤ –∫–ª–∞—Å—ñ –º–µ–Ω—é, –¥–ª—è –∑–±–µ—Ä—ñ–≥–∞–Ω–Ω—è –¥–∞–Ω–∏—Ö –ø—Ä–æ —ñ–¥–µ–Ω—Ç–∏—Ñ—ñ–∫–∞—Ç–æ—Ä –æ–±—Ä–∞–Ω–æ–≥–æ –≥–µ—Ä–æ—è –∑ –∫–æ–ª–µ–∫—Ü—ñ—ó 1 —è–∫–∏–π –±—Ä–∞—Ç–∏–º–µ—Ç—å—Å—è –∑ –ª–æ–∫–∞–ª—å–Ω–æ–≥–æ —Å—Ö–æ–≤–∏—â–∞.  
        this.syncWithLocalStorage(2); // –°—Ç–≤–æ—Ä—é—î–º–æ –∑–∞–ø–∏—Å –≤ –∫–ª–∞—Å—ñ –º–µ–Ω—é, –¥–ª—è –∑–±–µ—Ä—ñ–≥–∞–Ω–Ω—è –¥–∞–Ω–∏—Ö –ø—Ä–æ —ñ–¥–µ–Ω—Ç–∏—Ñ—ñ–∫–∞—Ç–æ—Ä –æ–±—Ä–∞–Ω–æ–≥–æ –≥–µ—Ä–æ—è –∑ –∫–æ–ª–µ–∫—Ü—ñ—ó 2 —è–∫–∏–π –±—Ä–∞—Ç–∏–º–µ—Ç—å—Å—è –∑ –ª–æ–∫–∞–ª—å–Ω–æ–≥–æ —Å—Ö–æ–≤–∏—â–∞. 

        this.playersCollection1 = generateHeroesCollection(HEROES); // –°—Ç–≤–æ—Ä—é—î–º–æ –º–∞—Å–∏–≤, –∫–æ–ª–µ–∫—Ü—ñ—é –≥–µ–≥–æ—ó–≤ (–æ–±'—î–∫—Ç –∑ –¥–∞–Ω–∏–º–∏) –¥–ª—è –ñ–∏–≤–æ–≥–æ –≥—Ä–∞–≤—Ü—è. 
        this.playersCollection2 = generateHeroesCollection(HEROES); // –°—Ç–≤–æ—Ä—é—î–º–æ –º–∞—Å–∏–≤, –∫–æ–ª–µ–∫—Ü—ñ—é –≥–µ–≥–æ—ó–≤ (–æ–±'—î–∫—Ç –∑ –¥–∞–Ω–∏–º–∏) –¥–ª—è –ö–æ–º–ø'—é—Ç–µ—Ä–∞.

        this.createGalleryMenu(this.playersCollection1, 1); // –°—Ç–≤–æ—Ä—é—î–º–æ –≥–∞–ª–µ—Ä–µ—é –≥–µ—Ä–æ—ó–≤ –¥–ª—è –ñ–∏–≤–æ–≥–æ –≥—Ä–∞–≤—Ü—è.  
        this.createGalleryMenu(this.playersCollection2, 2); // –°—Ç–≤–æ—Ä—é—î–º–æ –≥–∞–ª–µ—Ä–µ—é –≥–µ—Ä–æ—ó–≤ –¥–ª—è –ö–æ–º–ø'—é—Ç–µ—Ä–∞.

        if(!document.querySelector('.sound-gong')) {
            createSoundTrack({
                className: 'sound-gong',
                src: soundGong,
            });
        }
        
        await delay(4000).then(() => this.hideStartLoader());
        await delay(1000).then(() => this.showMenu());         
        await delay(0).then(() => {
            if (!document.querySelector('.sound-menu-bg')) {
                createSoundTrack({
                    className: 'sound-menu-bg',
                    src: soundMainMenuBg,
                    volume: 0.5,
                    controls: true,
                    loop: true,
                });
            }
        });         
    };
}

export default Menu;