import { formattingPlayerName } from '../logic';

/**
 * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≥–µ–Ω–µ—Ä—É–≤–∞–Ω–Ω—è –≤–∏–ø–∞–¥–∫–æ–≤–æ–≥–æ —á–∏—Å–ª–∞ –≤ –¥—ñ–∞–ø–∞–∑–æ–Ω—ñ –≤—ñ–¥ Min –¥–æ Max.
 * 
 * @param {number} range - –î—ñ–∞–ø–∞–∑–æ–Ω –≤—ñ–¥ 1 –¥–æ range.
 * @returns {number} - –í–∏–ø–∞–¥–∫–æ–≤–µ —á–∏—Å–ª–æ.
 */
export const getRandom = range => {
    return (range) ? Math.ceil(Math.random() * range) : 20;
}

/**
 * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≥–µ–Ω–µ—Ä—É–≤–∞–Ω–Ω—è –ø–æ—Ç–æ—á–Ω–æ–≥–æ —á–∞—Å—É.
 * 
 * @returns {string} - –ü–æ—Ç–æ—á–Ω–∏–π —á–∞—Å –≤ —Ñ–æ—Ä–º–∞—Ç—ñ (23:04:59).
 */
export const getTime = () => {
    const date = new Date();

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è —Ñ–æ—Ä–º–∞—Ç—É–≤–∞–Ω–Ω—è –ø–∞—Ä–∞–º–µ—Ç—Ä—ñ–≤ —á–∞—Å—É, –≥–æ–¥–∏–Ω–∏|—Ö–≤–∏–ª–∏–Ω–∏|—Å–µ–∫—É–Ω–¥–∏ –∑ —Ñ–æ—Ä–º–∞—Ç—É '2' –≤ —Ñ–æ—Ä–º–∞—Ç '02'.
     *
     * @param {number} param - –≥–æ–¥–∏–Ω–∏|—Ö–≤–∏–ª–∏–Ω–∏|—Å–µ–∫—É–Ω–¥–∏.
     * @returns {string} - –≥–æ–¥–∏–Ω–∏|—Ö–≤–∏–ª–∏–Ω–∏|—Å–µ–∫—É–Ω–¥–∏ –≤ —Ñ–æ—Ä–º–∞—Ç—ñ '02'.
     */
    const formatTime = param => {
        return (param < 10) ? '0' + param : param;
    }

    const hours = formatTime(date.getHours());
    const minutes = formatTime(date.getMinutes());
    const secunds = formatTime(date.getSeconds());
        
    return `${hours}:${minutes}:${secunds}`;
}

/**
 * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è —Å—Ç–≤–æ—Ä–µ–Ω–Ω—è –Ω–æ–≤–∏—Ö –î–û–ú –µ–ª–µ–º–µ–Ω—Ç—ñ–≤. 
 * 
 * @param {object} params - –ü–∞—Ä–∞–º–µ—Ç—Ä–∏ –Ω–æ–≤–æ–≥–æ –î–û–ú –µ–ª–µ–º–µ–Ω—Ç—É.
 * @param {string} params.tagName - –ù–∞–∑–≤–∞ —Ç–µ–≥–∞.
 * @param {string|object[]=} params.className - –ö–ª–∞—Å –µ–ª–µ–º–µ–Ω—Ç—É.
 * @param {object=} params.attrList - –ê—Ç—Ä–∏–±—É—Ç–∏ –µ–ª–µ–º–µ–Ω—Ç—É.
 * @param {string|HTMLElement=} params.content - –ù–∞–ø–æ–≤–Ω–µ–Ω–Ω—è –µ–ª–µ–º–µ–Ω—Ç—É.
 * @returns {HTMLElement} - HTML –µ–ª–µ–º–µ–Ω—Ç.
 */
export const createNewTag = ({tagName, className, attrList, content = ''}) => {
    const $newTag = document.createElement(tagName);

    const addClassAttr = value => {
        if (Array.isArray(value)) {
            value.forEach(item => {
                $newTag.classList.add(item);
            })
        } else {
            $newTag.classList.add(value);
        }
    }   
    
    if (className) {
        addClassAttr(className);        
    }

    if (attrList && typeCheck(attrList) === 'object') {
        const entries = Object.entries(attrList);
        
        entries.map(([key, value]) => {
            switch (key) {
                case 'className':
                    addClassAttr(value);
                    
                    break;  
                default:
                    $newTag.setAttribute(key, value);

                    break;
            }
        }); 
    }
        
    if (content && tagName !== 'ul' && tagName !== 'ol' && tagName !== 'img') {
        $newTag.innerHTML = content;
    }
    
    return $newTag;
}

/**
 * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø—Ä–∏—Ö–æ–≤—É–≤–∞–Ω–Ω—è —Å—Ç–æ—Ä—ñ–Ω–∫–∏.
 */
 export const hidePage = () => {
    document.querySelector('html').classList.add('disabled');
}

/**
 * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è —Å—Ç–æ—Ä—ñ–Ω–∫–∏.
 */
export const showPage = () => {
    document.querySelector('html').classList.remove('disabled');
}

/**
 * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–Ω—è –∑–∞—Ç—Ä–∏–º–∫–∏.
 * 
 * @param {number} [ms] - –ß–∞—Å –∑–∞—Ç—Ä–∏–º–∫–∏.
 * @returns {object} - –ü—Ä–æ–º—ñ—Å.
 */
export const delay = (ms = 0) => {
    return new Promise((resolve) => {
        setTimeout(() => {
            resolve();
        }, ms);
    });
};

/**
 * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø–µ—Ä–µ—Ç–≤–æ—Ä–µ–Ω–Ω—è —Å—Ç—Ä–æ—á–æ–∫ —Ä—ñ–∑–Ω–æ–≥–æ —Ñ–æ—Ä–º–∞—Ç—É –≤ —Ñ–æ—Ä–º–∞—Ç 'camelCase'.
 * 
 * @param {string} str - –°—Ç—Ä–æ—á–∫–∞ —Ñ–æ—Ä–º–∞—Ç—É 'aa-bb-cc' –∞–±–æ 'aa bb cc' –∞–±–æ 'aa_bb_cc'.
 * @param {boolean} firstChar - –Ø–∫—â–æ true —Ç–æ —Å—Ç—Ä–æ—á–∫–∞ –ø–æ—á–∏–Ω–∞—Ç–∏–º–µ—Ç—å—Å—è –∑ –≤–µ–ª–∏–∫–æ—ó –±—É–∫–≤–∏.
 * @returns {string} - –°—Ç—Ä–æ—á–∫–∞ —Ñ–æ—Ä–º–∞—Ç—É 'aaBbCc'.
 */
export const camelize = (str, firstChar) => {
    return str.replace(/(?:^\w|[A-Z]|\b\w)/g, (word, index) => {
        if (firstChar) {
            return word.toUpperCase();
        } else {
            return index === 0 ? word.toLowerCase() : word.toUpperCase();
        }
    }).replace(/[\s_-]+/g, '');
}

/**
 * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤–∏–∑–Ω–∞—á–µ–Ω–Ω—è —Ç–∏–ø—É –¥–∞–Ω–∏—Ö.
 * 
 * @param {*} value - –î–∞–Ω—ñ –±—É–¥—å —è–∫–æ–≥–æ —Ç–∏–ø—É.
 * @returns {string} - –ù–∞–∑–≤–∞ —Ç–∏–ø—É –¥–∞–Ω–∏—Ö.
 */
export const typeCheck = (value) => {
    const returnValue = Object.prototype.toString.call(value);
    
    const type = returnValue.substring(
        returnValue.indexOf(" ") + 1, 
        returnValue.indexOf("]"));

    return type.toLowerCase();
}

/**
 * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∑–∞–ø—É—Å–∫—É –∑–≤—É–∫–æ–≤–æ–≥–æ —Ñ–æ–Ω–æ–≤–æ–≥–æ —Å—É–ø—Ä–æ–≤–æ–¥–∂–µ–Ω–Ω—è. 
 * 
 * @param {object} params - –û–±'—î–∫—Ç –∑ –ø–∞—Ä–∞–º–µ—Ç—Ä–∞–º–∏ –¥–ª—è –Ω–∞–ª–∞—à—Ç—É–≤–∞–Ω–Ω—è –∑–≤—É–∫–æ–≤–æ—ó –¥–æ—Ä—ñ–∂–∫–∏.
 * @param {string} [params.id] - –ù–∞–∑–≤–∞ Id.
 * @param {string|object[]} [params.className] - –ù–∞–∑–≤–∞ –∫–ª–∞—Å—É.
 * @param {string} params.src - –ü—É—Ç—å –¥–æ –∑–≤—É–∫–æ–≤–æ–≥–æ —Ç—Ä–µ–∫—É.
 * @param {boolean} [params.controls] - –í—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è –ø–∞–Ω–µ–ª—ñ.
 * @param {boolean} [params.loop] - –ü—Ä–æ–≥—Ä–∞–≤–∞–Ω–Ω—è —Ç—Ä–µ–∫—É –ø–æ–∫–æ–ª—É.
 * @param {number} [params.volume] - –ì—É—á–Ω—ñ—Å—Ç—å –≤—ñ–¥ 0 - 1.
 * @param {boolean} [params.muted] - –ü—Ä–∏–≥–ª—É—à–µ–Ω–∞ –≥—É—á–Ω—ñ—Å—Ç—å.
 * @param {string} [params.gender] - –Ü–º'—è –≥–µ—Ä–æ—è –ø–µ—Ä–µ–¥–∞—î—Ç—å—Å—è —è–∫—â–æ —Ç—Ä–µ–±–∞ –≤—Å—Ç–∞–Ω–æ–≤–∏—Ç–∏ —Å—Ç–∞—Ç—å –¥–ª—è –∞—É–¥—ñ–æ.
 */
export const createSoundTrack = ({volume = 1, gender, ...rest}) => {
    const $body = document.querySelector('body');
    
    if (gender) {
        gender = formattingPlayerName(gender);

        if (rest.src && typeCheck(rest.src) === 'object' && (gender === 'sonya' || gender === 'sheeva' || gender === 'sindel')) { // –¢—Ä–µ–±–∞ –¥–ª—è –∞—É–¥—ñ–æ —è–∫—â–æ –≥–µ—Ä–æ–π –¥—ñ–≤—á–∏–Ω–∞.
            rest.src = rest.src.g;  // 'G', —â–æ –æ–∑–Ω–∞—á–∞—î –∂—ñ–Ω–æ—á–∏–π –≥–æ–ª–æ—Å.
        } else {
            rest.src = rest.src.m;  // 'M', —â–æ –æ–∑–Ω–∞—á–∞—î —á–æ–ª–æ–≤—ñ—á–∏–π –≥–æ–ª–æ—Å.
        }
    }

    const $el = createNewTag({
        tagName: 'audio',
        attrList: rest,
    });

    $el.volume = volume;        
    $el.play();

    $body.append($el);        
}

