import { getImg } from '../logic';
import { camelize } from '../utils';
import { ANIMATE } from '../constants';

class Player {
    constructor({id, name, hp = 100}){
        const nameToLoverCase = name.toLowerCase();

        this.createPropAnimate(nameToLoverCase);        
        this.id = id;
        this.name = name;
        this.hp = hp;
        this.avatar = getImg({type: 'avatars', name});
        this.biography = getImg({type: 'biography', name});
        this.currentImg = this.stance; // –¢—Ä–µ–±–∞ —â–æ–± –≤—ñ–¥–æ–±—Ä–∞–∑–∏–ª–∞—Å—è —Å—Ç–∞—Ä—Ç–æ–≤–∞ –ø–æ–∑–∏—Ü—ñ—è —Å—Ç–æ–π–∫–∏ –≥–µ—Ä–æ—è –ø–µ—Ä–µ–¥ –±–æ—î–º –Ω–∞ –∞—Ä–µ–Ω—ñ.
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è —Å—Ç–≤–æ—Ä–µ–Ω–Ω—è –≤–ª–∞—Å—Ç–∏–≤–æ—Å—Ç—ñ –∞–Ω—ñ–º–∞—Ü—ñ—ó (—Ç–æ–±—Ç–æ –∫–æ–º–∞–Ω–¥–∏ –¥–ª—è —Ä—É—Ö—ñ–≤) –≤ –æ–±'—î–∫—Ç—ñ –≥–µ—Ä–æ—è.
     *      
     * @param {string} nameToLoverCase - –Ü–º'—è –≥–µ—Ä–æ—è.
     */
    createPropAnimate = nameToLoverCase => {
        for(let key in ANIMATE) {            
            if (nameToLoverCase === key && ANIMATE[key].length) {      
                ANIMATE[key].forEach(item => {  
                    const attrName = camelize(item);
                    
                    this[attrName] = getImg({type: item, name: camelize(nameToLoverCase, true)});
                });
            }
        }
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤–∏—Ä–∞—Ö–æ–≤—É–≤–∞–Ω–Ω—è –∑–∞–ª–∏—à–∫—É –∂–∏—Ç—Ç—è –≥—Ä–∞–≤—Ü—è.
     * 
     * @param {number} damage - –ß–∏—Å–ª–æ –Ω–∞ —è–∫–µ –∑–º–µ–Ω—à–∏—Ç—å—Å—è –∂–∏—Ç—Ç—è –≥—Ä–∞–≤—Ü—è.
     */
    changeHP = damage => {    
        this.hp -= damage; // –í—ñ–¥–Ω—ñ–º–∞—î—Ç—å—Å—è –∂–∏—Ç—Ç—è.

        if (this.hp <= 0) {
            this.hp = 0;
        }
    };

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤–∏—è–≤–ª–µ–Ω–Ω—è –≥—Ä–∞–≤—Ü—è —è–∫–æ–º—É —Ç—Ä–µ–±–∞ –æ–Ω–æ–≤–∏—Ç–∏ –∑–∞–ª–∏—à–æ–∫ –∂–∏—Ç—Ç—è.
     * 
     * @returns {HTMLElement} - –ì—Ä–∞–≤–µ—Ü—å —è–∫–æ–º—É —Ç—Ä–µ–±–∞ –æ–Ω–æ–≤–∏—Ç–∏ –∑–∞–ª–∏—à–æ–∫ –∂–∏—Ç—Ç—è.
     */
    elHP = () => {
        return document.querySelector(`.player${this.playerNumber} .life`);
    };

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ–Ω–æ–≤–ª–µ–Ω–Ω—è –∑–∞–ª–∏—à–∫—É –∂–∏—Ç—Ç—è.
     */
    renderHP = () => {
        this.elHP().style.width = this.hp + '%';
    };
}

export default Player;



